"use client";

import Image from 'next/image';
import React , {useState} from 'react'
import { Monoton } from "next/font/google";
import { LockIcon } from 'lucide-react';

const monoton = Monoton({ subsets: ["latin"], weight: "400" });


const Sidebar = () => {
    const [showProjects, setShowProjects] = useState(true);
    const [showPriority, setShowPriority] = useState(true);

    const sidebarClassNames = 'fixed flex flex-col h[100%] justify-between shadow-xl transition-all duration-300 h-full z-40 dark;bg-black overflow-y-auto bg-white w-64';

  return (
    <div className={sidebarClassNames}>
        <div className='flex h-[100%] w-full flex-col justify-start'>
            {/* Toplogo */}
         <div className="z-50 flex min-h-[56px] w-64 items-center justify-between bg-white px-6 pt-3 dark:bg-blacl">
          <div className={`text-3xl font-bold text-turquoise dark:text-white ${monoton.className}`}>  
                          TASKLYTICS
            </div>
        </div>
        {/* Team */}
        <div className='flex items-center gap-5 border-y-[1.5px] border-gray-200 px-8 py-4 dark:border-gray-700'>
        <Image src="/logo.png" alt="logo" width={60} height={40} />
        <div>
            <h3 className='text-md font-bold tracking-widest dark:text-gray-200'>
                MY TEAM
            </h3>
            <div className='mt-1 flex items-start gap-2'>
               <LockIcon className='mt-1[0.1rem] h-3 text-gray-500 dark:text-gray-400' />
                <p className='text-xs text-gray-500'>Private</p>
            </div>
        </div>
    </div>   
    {/* NAVBARLINKS */}  
    </div>
    </div>
    
  )
}

export default Sidebar